@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;500&family=Source+Code+Pro:wght@500&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.container-head {
  text-align: center;
  height: 100vh;
}
.name-styling {
  margin-top: 20%;
  letter-spacing: 0.6em;
  word-break: break-all;
  color: aliceblue;
  word-break: break-all;
  font-size: 4vw;
  font-family: Barlow, sans-serif;
  font-weight: 900;
}

.name-styling-subheading {
  letter-spacing: 0.3em;
  margin-top: 1em;
  font-size: 2.3vw;
  word-break: break-all;
  color: rgb(190, 195, 199);
  font-family: Barlow, sans-serif;
}
.jumbo {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5))),
    url(/static/media/BLack.b98b6475.jpg) no-repeat center center;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/BLack.b98b6475.jpg) no-repeat center center;
  width: 100%;
  height: 100vh;
}
.what-i-do {
  font-size: 80px;
  font-family: Barlow, sans-serif;
  color: rgb(63, 64, 65);
  margin: 1%;
  margin-top: 2%;
  font-weight: 900;
  border-bottom: 1px;
}

.what-i-do-text {
  font-size: 40px;
  font-family: Barlow, sans-serif;
  text-align: justify;
  color: rgb(63, 64, 65);
}

.navbar {
  opacity: 0.9;
  background-color: black;
  font-family: Barlow, sans-serif;
}

.marquee {
  font-weight: 200;
  font-size: 35px;
  font-style: oblique;
  color: rgb(63, 64, 65);
  -webkit-text-stroke-color: rgb(63, 64, 65);
}

.skills {
  font-family: Dosis, sans-serif;
  height: 100vh;
}

.a {
  width: 1px;
  height: 1px;
}

.imagee {
  text-align: center;
  margin: 1%;
  font-size: 30px;
  font-family: Barlow, sans-serif;
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}
a {
  text-decoration: none;
}

.showcase{
  object-fit: fill;
  height: 100%;
  width: 100%;
  -webkit-transition: -webkit-transform 1s ease-in-out;
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}
.showcase:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.temp {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: auto;
  justify-content: center;
}

.codeLineNumber {
  font-size: 18px;
  font-family: Source Code Pro, monospace;
  width: 0.1%;
  color: #ffffff;
}

@media (max-width: 1000px) {
  .name-styling {
    font-size: 5vw;
  }
}

@media (max-width: 800px) {
  .what-i-do-text {
    font-size: 30px;
  }
  .codeLineNumber {
    font-size: 15px;
  }
  .name-styling-subheading {
    font-size: 2vw;
  }
}
@media (max-width: 550px) {
  .what-i-do-text {
    font-size: 25px;
  }
  .what-i-do {
    font-size: 45px;
  }
  .codeLineNumber {
    font-size: 12px;
  }
  .imagee {
    font-size: 20px;
  }
}
@media (max-width: 400px) {
  .what-i-do {
    font-size: 40px;
  }
  .codeLineNumber {
    font-size: 10px;
  }
  .imagee {
    font-size: 15px;
  }
}

